module.exports = "<pre class=\"shiki\" style=\"background-color: #1E1E1E\"><code><span style=\"color: #569CD6\">import</span><span style=\"color: #D4D4D4\"> { </span><span style=\"color: #9CDCFE\">Behavior</span><span style=\"color: #D4D4D4\"> } </span><span style=\"color: #569CD6\">from</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #CE9178\">\"behavior-state\"</span><span style=\"color: #D4D4D4\">;</span>\n<span style=\"color: #569CD6\">const</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #9CDCFE\">$a</span><span style=\"color: #D4D4D4\"> = </span><span style=\"color: #569CD6\">new</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #4EC9B0\">Behavior</span><span style=\"color: #D4D4D4\">(</span><span style=\"color: #CE9178\">\"initial value\"</span><span style=\"color: #D4D4D4\">);</span>\n<span style=\"color: #9CDCFE\">$a</span><span style=\"color: #D4D4D4\">.</span><span style=\"color: #DCDCAA\">next</span><span style=\"color: #D4D4D4\">(</span><span style=\"color: #CE9178\">\"new value\"</span><span style=\"color: #D4D4D4\">);</span>\n\n<span style=\"color: #608B4E\">// React component</span>\n<span style=\"color: #808080\">&lt;</span><span style=\"color: #569CD6\">$a.react</span>\n<span style=\"color: #D4D4D4\">  </span><span style=\"color: #608B4E\">// on every render</span>\n<span style=\"color: #D4D4D4\">  </span><span style=\"color: #9CDCFE\">next</span><span style=\"color: #D4D4D4\">={</span><span style=\"color: #9CDCFE\">value</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #569CD6\">=&gt;</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #CE9178\">\"a = \"</span><span style=\"color: #D4D4D4\"> + </span><span style=\"color: #9CDCFE\">value</span><span style=\"color: #D4D4D4\">}</span>\n<span style=\"color: #808080\">/&gt;</span><span style=\"color: #D4D4D4\">;</span></code></pre>";