module.exports = "<pre class=\"shiki\" style=\"background-color: #1E1E1E\"><code><span style=\"color: #569CD6\">import</span><span style=\"color: #D4D4D4\"> { </span><span style=\"color: #9CDCFE\">Behavior</span><span style=\"color: #D4D4D4\"> } </span><span style=\"color: #569CD6\">from</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #CE9178\">'behavior-state'</span>\n\n<span style=\"color: #569CD6\">const</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #9CDCFE\">state</span><span style=\"color: #D4D4D4\"> = </span><span style=\"color: #569CD6\">new</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #4EC9B0\">Behavior</span><span style=\"color: #D4D4D4\">(</span><span style=\"color: #B5CEA8\">1</span><span style=\"color: #D4D4D4\">)</span>\n\n<span style=\"color: #9CDCFE\">state</span><span style=\"color: #D4D4D4\">.</span><span style=\"color: #9CDCFE\">value</span><span style=\"color: #D4D4D4\"> == </span><span style=\"color: #B5CEA8\">1</span>\n\n<span style=\"color: #9CDCFE\">state</span><span style=\"color: #D4D4D4\">.</span><span style=\"color: #DCDCAA\">next</span><span style=\"color: #D4D4D4\">(</span><span style=\"color: #B5CEA8\">2</span><span style=\"color: #D4D4D4\">)</span>\n\n<span style=\"color: #9CDCFE\">state</span><span style=\"color: #D4D4D4\">.</span><span style=\"color: #9CDCFE\">value</span><span style=\"color: #D4D4D4\"> == </span><span style=\"color: #B5CEA8\">2</span>\n\n<span style=\"color: #9CDCFE\">state</span><span style=\"color: #D4D4D4\">.</span><span style=\"color: #DCDCAA\">subscribe</span><span style=\"color: #D4D4D4\">(</span><span style=\"color: #9CDCFE\">nextValue</span><span style=\"color: #D4D4D4\"> </span><span style=\"color: #569CD6\">=&gt;</span><span style=\"color: #D4D4D4\"> {</span>\n<span style=\"color: #D4D4D4\">  </span><span style=\"color: #4EC9B0\">console</span><span style=\"color: #D4D4D4\">.</span><span style=\"color: #DCDCAA\">log</span><span style=\"color: #D4D4D4\">(</span><span style=\"color: #9CDCFE\">nextValue</span><span style=\"color: #D4D4D4\">)</span>\n<span style=\"color: #D4D4D4\">}) </span><span style=\"color: #608B4E\">// immediately prints current value, 2</span>\n\n<span style=\"color: #9CDCFE\">state</span><span style=\"color: #D4D4D4\">.</span><span style=\"color: #DCDCAA\">next</span><span style=\"color: #D4D4D4\">(</span><span style=\"color: #B5CEA8\">5</span><span style=\"color: #D4D4D4\">) </span><span style=\"color: #608B4E\">// =&gt; prints 5</span></code></pre>";